import React, { useEffect } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import NetworkProvider, { useNetworkContext } from "./contexts/NetworkContext";
import TokenVestingApp from "./views/TokenVestingApp";
import { Grid, Row, Col } from "react-bootstrap";

const App = () => (
  <NetworkProvider>
    <Router>
      <Switch>
        <Route path="/:address/:token?" component={Main} />
        <Route component={MissingAddress} />
      </Switch>
    </Router>
  </NetworkProvider>
);

const Main = function ({ match }) {
  const {
    web3,
    currentProvider,
    setIsEvmosRequired,
    web3Modal,
    restoreToDefaultNetworkSettings,
    connectWallet,
  } = useNetworkContext();
  let { address, token } = match.params;
  useEffect(() => {
    // if token is obtained, it requires FUJI
    const isEvmos = !!token;
    setIsEvmosRequired(isEvmos);
    if (web3Modal.cachedProvider) {
      connectWallet();
    } else {
      restoreToDefaultNetworkSettings(isEvmos);
    }
    // only run on start
    // eslint-disable-next-line
  }, []);

  return currentProvider && web3 && web3.utils.isAddress(address) ? (
    <TokenVestingApp
      currentProvider={currentProvider}
      address={address}
      token={token || "0xe2c5d47277C2c539f0F6A51F11f0C5161c22fbc9"}
    />
  ) : (
    <MissingAddress />
  );
};


const MissingAddress = () => (
<Grid>
  <Row>
    <Col xs={12} md={12}>
      <div><h1>A little lost?</h1></div>
    </Col>
    </Row>
    <Row>
    <Col xs={12} md={6}>
    <p>Enter your Vesting Contract Address after the website domain. For example: <a href="https://vesting.evmos.community/0x73623379bfFf6D43d5E8DC12e31D75007AEA63DD">https://vesting.evmos.community/0x73623379bfFf6D43d5E8DC12e31D75007AEA63DD</a></p>

    </Col>
  </Row>
</Grid>
);

export default App;
